<template slot-scope="scope">
  <div>
    <!-- 结尾 -->
    <div class="the_end">
      <div>
        <div class="starsec" />
        <div class="starthird" />
        <div class="starfourth" />
        <div class="starfifth" />
      </div>
      <!--Dust particle end--->

      <div class="lamp__wrap">
        <div class="lamp">
          <div class="cable" />
          <div class="cover" />
          <div class="in-cover">
            <div class="bulb" />
          </div>
          <div class="light" />
        </div>
      </div>
      <!-- END Lamp -->
      <div class="error">
        <!-- Content -->
        <div class="error__content">
          <div class="error__message message">
            <h1 class="message__title">{{ title }}</h1>
            <p class="message__text">{{ text }}</p>
          </div>
          <div class="error__nav e-nav">
            <span class="e-nav__link" @click="callBack" />
          </div>
        </div>
        <!-- END Content -->
      </div>
    </div>
  </div>
</template>
<script>
const res = require('@/config.js')
import { reactive, toRefs } from 'vue'
export default {
  name: 'TheEnd',
  setup () {
    // 赋值
    const data = reactive({
      title: res.the_end.title,
      text: res.the_end.text
    })
    const callBack = () => {
      window.scrollTo({ top: 0, behavior: 'smooth' })
    }
    // 结构导出
    const dataOut = toRefs(data)
    return { callBack, ...dataOut }
  }
}
</script>
